
import {injectReducer} from "../../store/reducers"
import Constant from '../../components/lib/Constant'
export default((store)=>({
    path: Constant.router_preffix + '/special_invoice',
    getComponent(nextState,cb){
        require.ensure([],(require)=>cb(null,require('./components/SpecialInvoice').default,'special_invoice'))
    }
}))


