import { injectReducer } from '../../store/reducers'
import Constant from '../../components/lib/Constant'
export default(store) => ({
    path: Constant.router_preffix + '/ticket_type',
    getComponent(nextState, cb) {
        require.ensure([], (require) => cb(null, require('./components/TicketTypes').default,'/ticket_type'))
    }
})


